import React from "react";
import styles from "./Header.module.css"

const Header = (props) => {
    return (
        <div className={styles.header}>
            <a href="https://social-network.samuraijs.com/docs" target = "_blank" >Server API (temporary link)</a>
            <div className={styles.logInControls}>
                {props.isLoggedIn ? <span><span className={styles.email}> {props.email}</span><span>{props.login}</span></span> : <span><input onChange={props.onEmailInputChange} placeholder="email pls" value={props.emailInput}/><input onChange={props.onPasswordInputChange} type="password" placeholder="password pls" value={props.passwordInput}/></span>}<button onClick={props.logInOut}>{props.isLoggedIn ? 'üëã' : 'üîê'}</button>
            </div>
        </div>
    )
}

export default Header